import React from "react"
import { encodeObj, copyToClipboard } from "../../utils"
import { Button, Space, Divider } from "antd"
import { SmileTwoTone, RocketTwoTone } from "@ant-design/icons"
import CreateForm from "./CreateForm"
import TweenOne from "rc-tween-one"
import BezierPlugin from "rc-tween-one/lib/plugin/BezierPlugin"
import css from "./styles.module.scss"
TweenOne.plugins.push(BezierPlugin)

const animation = function () {
  return {
    bezier: {
      type: "soft",
      autoRotate: true,
      vars: Array(20)
        .fill(null)
        .map((v, i) => ({ x: i * 100 + Math.random() * 100, y: Math.random() * 100 })),
    },
    delay: Math.random() * 1000,
    repeat: -1,
    duration: 20000,
  }
}

let a1 = animation()
let a2 = animation()
let a3 = animation()

const Home = () => {
  const [url, setUrl] = React.useState("")
  return (
    <div className={css.container}>
      <h1>Game Protected URL</h1>
      <Divider plain>Protect your urls in a funny way</Divider>
      <CreateForm onValuesChange={(_, values) => setUrl(window.location + encodeObj(values.games))}>
        <Divider plain>Are you done?</Divider>
        <Space>
          <Button href={url} target="_blank">
            <SmileTwoTone />
            Try
          </Button>
          <span>or</span>
          <Button onClick={() => copyToClipboard(url)}>Copy URL</Button>
          <span>or</span>
          <Button href={`https://tinyurl.com/create.php?url=${encodeURIComponent(url)}`} target="_blank" children="Tiny URL" />
        </Space>
      </CreateForm>
      <TweenOne animation={a1} style={{ margin: 0, position: "absolute", left: -100 }}>
        <RocketTwoTone rotate={90} />
      </TweenOne>
      <TweenOne animation={a2} style={{ margin: 0, position: "absolute", left: -200 }}>
        <RocketTwoTone rotate={90} />
      </TweenOne>
      <TweenOne animation={a3} style={{ margin: 0, position: "absolute", left: -300 }}>
        <RocketTwoTone rotate={90} />
      </TweenOne>
    </div>
  )
}

export default Home
